//
// Variables
// --------------------------------------------------

// Fonts
// -------------------

$headingFont: 'Trebuchet MS', sans-serif;
$paragraphFont: 'Helvetica', sans-serif;


// Colours
// -------------------

$darkPrimaryColor: #E64A19;
$primaryColor: #FF5722;
$lightPrimaryColor: #FFCCBC;

$textIconColor: #FFFFFF;
$accentColor: #03A9F4;
$primaryTextColor: #212121;
$secondaryTextColor: #757575;
$dividerColor: #BDBDBD;

$secondaryColor: #536DFE;
$separatorColor: rgba(0,0,0,0.1);

$textColor: #000000;
$baseColor: #EEEEEE;

$socialFacebook: #3B5998;
$socialTwitter: #00ACED;
$socialInstagram: #E3569A;
$socialGithub: #7B63AB;
$socialLinkedin: #0077B5;

$errorColor: #AA0000;
$successColor: #00AA00;
$baseFontSize: 16;

// Breakpoints
// -------------------

$breakpoints: (
	'mobile': (max-width: 736px),
	'tablet': (max-width: 1024px),
	'desktop': (min-width: 1200px),
);

// Include gutter on outside 
$gutterOnOutside: true;

// Breakpoints Grid Settings
// -------------------

$grid-settings: (
	base: (
		container-columns: 12,
		gutter: 1%,
	),
	desktop: (
		container-columns: 12,
		gutter: 1%,
	),
	tablet: (
		container-columns: 12,
		gutter: 5%,
	),
	mobile: (
		container-columns: 1,
		gutter: 5%,
	)
);
