@import 'variables.scss';
@import 'mixins/_grid.scss';
@import 'mixins/_helpers.scss';

/* LOGIN FORM */

body.login {
	#header {
	    height: auto;
	    padding: 15px 16px;
	    justify-content: center;
		border-radius: 5px 5px 0px 0px;
		h1 {
		    font-size: 18px;
			a {
			    color: #fff;
			}
		}
	}
	#content {
	    padding: 20px 20px 0;
	}
	#container {
		@include card-style(#FFF, $primaryTextColor);
		border-radius: 5px;
	    overflow: hidden;
	    width: 28em;
	    min-width: 300px;
	    margin: 100px auto;
	}
	#content-main {
	    width: 100%;
	}
	.form-row {
	    padding: 4px 0;
	    float: left;
	    width: 100%;
	    border-bottom: none;
		label {
		    padding-right: 0.5em;
		    line-height: 2em;
		    font-size: 1em;
		    clear: both;
		    color: #333;
		}
		#id_username, #id_password {
		    clear: both;
		    padding: 8px;
		    width: 100%;
		    -webkit-box-sizing: border-box;
		       -moz-box-sizing: border-box;
		            box-sizing: border-box;
		}
	}
	span.help {
	    font-size: 10px;
	    display: block;
	}
	.submit-row {
	    clear: both;
	    padding: 1em 0 0 9.4em;
	    margin: 0;
	    border: none;
	    background: none;
	    text-align: left;
	}
	.password-reset-link {
	    text-align: center;
	}
}